<template>
    <div class="container title__wrapper">
        <h1 class="title">
            {{text}}
        </h1>
        <Logo v-if="withLogo" />
    </div>
</template>

<script>
    import { defineAsyncComponent, defineComponent } from "vue";

    export default defineComponent({
        name: "MainTitle",
        components: {
            Logo: defineAsyncComponent(() => import("@/components/Logo.vue")),
        },
        props: {
            text: {
                type: String,
                required: true,
            },
            withLogo: {
                type: Boolean,
                default: false
            }
        },
    });
</script>

<style scoped>
.title__wrapper {
  display: flex;
  width: 100%;
  margin-top: 24px;
  font-family: "SF Pro", serif;
  color: #061029;
}

.title {
  font-weight: 600;
  font-size: 22px;
  line-height: 26px;
  letter-spacing: -0.02em;
  color: #061029;
}

</style>